.form {
    background-image: linear-gradient(
        110deg,
        rgba(255, 255, 255, 1) 0%,
        rgba(255, 255, 255, 1) 50%,
        transparent 50%
        ),
        url("../geranimo-WJkc3xZjSXw-unsplash.jpg");
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    width: 100%;
    height: 30rem;
    border-radius: 12px;
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.3);
    padding: 20px;
}
.custom-form {
    width: 50%;


}
.form-control {
    position: relative;
    margin-bottom: 20px;
}
.form-control label,
.form-control input {
    display: block;
}

.form-control input {
    padding: 20px;
    outline: none;
    border-radius: 6px;
    border: 0;
    background-color: white;
    width: 80%;
    border-bottom: 3px solid transparent;
    font-family: "Lato", sans-serif;
    box-shadow: 0 0 3px rgb(233, 233, 233) inset;


    
}
.form-control input:focus:valid {
    border-bottom: 3px solid green;
}
.form-control input:focus:not(:valid) {
    border-bottom: 3px solid orangered;
    
}
.form-control label {
    position: absolute;
    left: 0px;
    top: -20px;
    font-weight: bold;
    color:grey;
    transition: 0.3s;
    
}


.form-control input:placeholder-shown + label {
    top: 20px;
    opacity: 0;
    visibility: hidden;


}

.custom-form h1 {
    color: rgba(6, 196, 79, 0.8);
}
.icon-span {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    width: 100%;
}
.my-icon {
    width: 230px;
    height: 230px;
    margin-top: -94px;
    padding: 4px;
    margin-right: 46px;
    align-self: end;
}
.foot-note {
    font-size: 0.9rem;
    align-self: flex-start;
    
}
.foot-note > a {
    color:grey;
    font-weight: bold;
}